import React from 'react';
import {
    CurrentVocab,
    CurrentVocabWrapper
} from './CurrentVocab.styled.js'
function Example(props) {
    const parseCurrentVocab = props.currentVocab.split(' ').map(char => {
        const correct = props.guessVocab.search(char) > -1
        return {
            char,
            correct
        }
    })
    return (
        <CurrentVocabWrapper likelyPercent={props.likelyPercent}>
            <CurrentVocab>{parseCurrentVocab.map((item, index) => (
                <span className={item.correct ? 'text-green' : ''} key={`${item.char}${index}`}>{item.char}{' '}</span>
            ))}</CurrentVocab>
        </CurrentVocabWrapper>
    );
}
export default Example;
