import React from 'react';
import { GuessVocab, GuessVocabWrapper } from './GuessVocab.styled.js'
function Example(props) {
    const is_correct = props.likelyPercent() === 100
    return (
        <GuessVocabWrapper className={is_correct && 'gradient-border'} likelyPercent={props.likelyPercent}>
            <GuessVocab className={is_correct && 'gradient-text'}>
                {props.guessVocab}
            </GuessVocab>
        </GuessVocabWrapper>
    );
}
export default Example;
