import React from 'react';
import { MicButton } from './SpeechRecognition.styled'

function Example(props) {


    return (
        <MicButton onClick={props.listening ? props.stop : props.listen} >
            <span className={props.listening ? 'icon-mic' : 'icon-mute'}></span>
        </MicButton>
    );
}
export default Example;
