import './App.css';
import './assets/fontello/css/fontello.css'
import vocabList from './data/sentences.json'
import SpeechRecognition from './components/SpeechRecognition'
import SpeechSynthesis from './components/SpeechSynthesis'
import React, { useState } from 'react';
import similarity from './helpers/similarity.helper'
import GuessVocab from './components/GuessVocab'
import CurrentVocab from './components/CurrentVocab'
import { useSpeechRecognition } from 'react-speech-kit';
import { AppWrapper } from './App.styled'
import { useDetectAdBlock } from "adblock-detect-react";

function App() {
  const getRandomIndex = () => {
    const max = vocabList.data.length
    return Math.floor(Math.random() * max);
  }
  const adBlockDetected = useDetectAdBlock();
  const [randomIndex, setRandomIndex] = useState(getRandomIndex());
  const [currentVocab, setCurrentVocab] = useState(vocabList.data[randomIndex].replace('.', '').replace(',', '').toUpperCase());
  const [guessVocab, setGuessVocab] = useState('');
  const [score, increseScore] = useState(0);
  const guessChange = (result) => {
    const newGuess = result.replace('.', '').replace(',', '')
    guessVocabChange(newGuess)
  }
  const { listen, listening, stop } = useSpeechRecognition({
    onResult: guessChange.bind(this),
  });
  const likelyPercent = () => {
    const likely = similarity(guessVocab, currentVocab) * 100
    if (likely === 100 && listening) {
      stop()
      increseScore(score + 1)
    }
    return likely
  }
  const currentGuessVocab = () => guessVocab.toUpperCase();
  const guessVocabChange = (txt) => {
    setGuessVocab(txt)

  }
  const skipVocab = () => {
    setGuessVocab('')
    const nextIndex = getRandomIndex()
    setRandomIndex(nextIndex);
    setCurrentVocab(vocabList.data[nextIndex].replace('.', '').replace(',', '').toUpperCase())
    // if (!listening) {
    //   listen()
    // }
  }

  return (
    <AppWrapper className="App">
      <header className="App-header">
        {adBlockDetected ? <React.Fragment>
          <h1>Can you disabled ads blocker 🥺</h1>
          <h2>I just need to run ads for my coffee time</h2>
        </React.Fragment> : <React.Fragment>
          <h4>Score: {score}</h4>
          <CurrentVocab likelyPercent={likelyPercent} guessVocab={currentGuessVocab()} currentVocab={currentVocab} />
          <GuessVocab likelyPercent={likelyPercent} guessVocab={currentGuessVocab()}></GuessVocab>
          <SpeechRecognition listen={listen} listening={listening} stop={stop} />
          {/* <SpeechSynthesis currentVocab={currentVocab} /> */}
          <button onClick={skipVocab}>skip</button>
        </React.Fragment>
        }
      </header>
    </AppWrapper>
  );
}

export default App;
